@import './variables';
@import './main.scss';
@import './mainContent.scss';
@import './intro';
// @import './cursor';
@import './menu';

/********* GENERAL *********/

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $font1;
    letter-spacing: 1px;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing:antialiased;
    color: $white;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    @media all and (max-width: $mobile) {
        background: $black;
    }
}

a { text-decoration: none; color: inherit; }

section {
    position: absolute;
    overflow: hidden;
    @media all and (max-width: $mobile) {
        overflow-y: visible;
    }
}

.link:hover {
    color: $main1;
    transition: all 500ms ease;
    cursor: pointer;
}